import React, { useState, useEffect } from "react";
import { Container, Typography, Paper, Button, Box, Divider } from "@mui/material";
import Link from "next/link";
import LiveDataCards from "../components/LiveDataCards"; // ✅ Import LiveDataCards
import '../styles/global.css'

const API_URL = "http://localhost:8000/data";
const WS_URL = "ws://localhost:8000";

export default function Home() {
  const [data, setData] = useState({});

  useEffect(() => {
    fetch(API_URL)
      .then((res) => res.json())
      .then((initialData) => {
        setData(initialData[initialData.length - 1]); // ✅ Load latest data
      });

    const socket = new WebSocket(WS_URL);
    socket.onmessage = (event) => {
      const newData = JSON.parse(event.data);
      setData(newData); // ✅ Update real-time data
    };

    return () => socket.close();
  }, []);

  return (
    <>
      {/* ✅ Main Welcome Section */}
      <Container maxWidth="lg" sx={{ py: 8, textAlign: "center" }}>
        <Paper elevation={4} sx={{ p: 5, borderRadius: 3, boxShadow: 3 }}>
          <Typography variant="h3" color="primary" fontWeight="bold" gutterBottom>
            Welcome to the Real-Time Dashboard
          </Typography>
          <Typography variant="h6" color="textSecondary" paragraph>
            Monitor live air quality data with real-time updates.
          </Typography>

          <Box mt={3}>
            <Link href="/dashboard" passHref>
              <Button variant="contained" color="primary" size="large" sx={{ borderRadius: 2 }}>
                Go to Dashboard
              </Button>
            </Link>
          </Box>
        </Paper>
      </Container>

      {/* ✅ Aligned Live Data Section */}
      <Container maxWidth="lg" sx={{ mt: 4 }}> 
        <Paper
          elevation={3}
          sx={{
            p: 4,
            borderRadius: 3,
            border: "2px solid #007bff", // ✅ Blue border
            boxShadow: 3,
            textAlign: "center",
          }}
        >
          <Typography variant="h4" color="primary" gutterBottom>
            Live Data Overview
          </Typography>
          <Divider sx={{ my: 2 }} />
          <LiveDataCards data={data} />
        </Paper>
      </Container>
    </>
  );
}
